<script setup lang="ts">
import { computed, toRefs } from 'vue'

export type Props = {
  count?: number
  total?: number
  initialProgress?: number
  displayCount?: boolean
}

const props = defineProps<Props>()
const { count, total, initialProgress } = toRefs(props)

const progress = computed(() => {
  if (initialProgress.value) {
    return initialProgress.value / 100
  }

  if (count.value && total.value) {
    return count.value / total.value
  }
  return 0
})

const roundedProgress = computed(() => (progress.value * 100).toFixed(0))

const circumference = 30 * 2 * Math.PI
</script>

<template>
  <div class="flex items-center justify-center">
    <svg class="h-12 w-12 rounded-full border md:h-12 md:w-12" viewBox="10 10 60 60">
      <circle
        :class="[progress < 1 ? 'text-gray-300' : 'text-red-500']"
        stroke-width="7"
        stroke="currentColor"
        fill="transparent"
        r="30"
        cx="40"
        cy="40"
        transform="rotate(-90 40 40)"
      />
      <circle
        v-if="progress <= 1"
        class="text-blue-500"
        :class="[roundedProgress === '100' && 'text-green-500']"
        stroke-width="7"
        :stroke-dasharray="circumference"
        :stroke-dashoffset="circumference - progress * circumference"
        stroke-linecap="round"
        stroke="currentColor"
        fill="transparent"
        r="30"
        cx="40"
        cy="40"
        transform="rotate(-90 40 40)"
      />
    </svg>

    <span
      class="
      absolute cursor-default text-xs
      text-blue-500 dark:text-blue-300
      "
      :class="[displayCount ? 'text-base font-bold' : '']"
    >
      {{ displayCount ? count : `${roundedProgress}%` }}
    </span>
  </div>
</template>
